<template>
    <el-row>
        <el-col :xs="24" :sm="24" :md="{ span: 16, offset: 4 }" :lg="{ span: 14, offset: 5 }" :xl="{ span: 12, offset: 6 }">
            <el-container id="bk">
                <el-header>
                    <img class="logo" src="static/logo.png"></img>
                    <div class="slogan">提供私人富文本信息加密存储与回取服务</div>
                </el-header>
                <el-main>
                    <div class="main">
                        <quill-editor id="editor" class="editor" ref="editor" :options="editorOption" :disabled='step > 1'
                            v-model="content"></quill-editor>
                        <div class="tip">
                            <p>使用说明：</p>
                            <ul>
                                <li>网站会给你一个存储链接，可用于向第三方应用提供内容 </li>
                                <li>网站只存储加密后的信息，请自行记住密码，自行解密</li>
                                <li>或者把下载的内容粘贴在这里帮你解密</li>
                                <li>加密算法采用标准AES</li>
                                <li>信息大小限制为5M，超过无法上传</li>
                                <li>信息免费保留24小时或免费下载2次，超限删除</li>
                                <li>如果需要更多的保留时间和下载次数请联系QQ：642128162</li>
                            </ul>

                        </div>
                        <div class="tool" v-if="!bShowCaptcha">
                            <el-input class="input" v-model="password" placeholder="请输入密码"></el-input>
                            <el-button type="primary" class="btn" @click="onEncryp" size="small">加密</el-button>
                            <el-button type="primary" class="btn" @click="onDecryp" size="small">解密</el-button>
                        </div>
                        <div v-else title="人机验证">
                            <img style="width:144px; height:144px; margin: 0 auto;" :src="qrCode" alt="QR Code" />
                            <p style="color:aliceblue; font-size: 14px; text-align: center; padding: 10px 0;">为避免资源滥用，请用微信扫码证明您是真实用户, 如果您用手机使用本网站，请截图后再用微信扫码</p>
                        </div>

                    </div>

                </el-main>
                <el-footer class="footer">
                    <el-link type="primary" href="https://beian.miit.gov.cn/">蜀ICP备2021013463号-2</el-link>
                </el-footer>
            </el-container>
            
            <div class="uploadprogress" v-if="bUploadDlg">
                <div class='uploadcenter'>
                    <block v-if="uploadProgress === 100">
                        <el-progress class="progressCircle" type="circle" :stroke-width="12" :width="200"
                            v-if="uploadProgress === 100" :percentage="uploadProgress" status="success"></el-progress>

                        <p class="uploadtip">请记住你的下载链接: <span style="color:#F56C6C;">{{ downloadUrl }}</span></p>
                        <div class="toolbar">
                            <el-button class="uploadbtn" type="success" @click="onCopyUrl" size="small">复制链接</el-button>
                            <el-button class="uploadbtn" type="success" @click="onUploadOK" size="small">确定</el-button>
                        </div>
                    </block>
                    <block v-else>
                        <el-progress class="progressCircle" type="circle" :stroke-width="12" :width="200"
                            :percentage="uploadProgress"></el-progress>
                        <div class="uploadtip">上传速度: {{ uploadSpeed }}</div>
                        <el-button class="uploadbtn" @click="onCancleUpload" type="danger" size="small">取消上传</el-button>
                    </block>
                </div>
            </div>
        </el-col>
    </el-row>
</template>

<style scoped>
#bk {
    height: 100vh;
    width: 100%;
    background-color: #161823;
    font-size: 18px;
    /* display: flex;
    flex-direction: column; */
}

.main {
    min-height: 500px;
    border: 1px solid #333;
    box-sizing: border-box;
    margin: 4px;
    width: 100%;
}

.logo {
    width: 1rem;
    height: 1rem;
    margin: 4px auto;
    display: block;
}

.footer {
    width: 100%;
    display: block;
    text-align: center;
    padding: 10px 0;
}

.slogan {
    text-align: center;
    margin: 8px;
    font-weight: 200;
    color: #fff;
}

.quill-editor {
    /* border: 1px solid #ddd; */
    background-color: #e3f9fd;
    width: 100%;
    min-height: 500px;
    flex: auto;
}

.ql-container.ql-snow {
    border-color: none;
}

.tool {
    padding: 10px;
    display: flex;
    justify-content: space-around;
}

.input {
    width: 200px;
    margin: 0 10px;
}

.btn {
    margin: 0 10px;
}

.tip {

    padding: 10px 30px;
    color: #fff;
    font-size: 12px;
}


.uploadprogress {
    width: 100%;
    height: 100%;
    background-color: #000;
    filter: alpha(opacity=80);
    -moz-opacity: 0.8;
    opacity: 0.8;
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 1000;
}

.uploadcenter {
    position: relative;
    width: 500px;
    height: 300px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.progressCircle {
    left: 50%;
    transform: translateX(-50%);
}

.uploadcell {
    flex: auto;
    text-align: center;
    color: #fff;
    width: 100px;
    height: 40px;
    line-height: 40px;
}

.toolbar {
    width: 100%;
    display: flex;
    justify-content: space-around;
}
</style>

<script>

const util = require('../util')

var _taskID = null

export default {
    name: 'bk',
    components: {},
    data() {
        return {
            bUploadDlg: false,
            downloadUrl: '',
            content: '',
            password: '',
            bShowCaptcha: false,
            authCode: ''
        }
    },
    methods: {
        async onEncryp() {
            if (!this.password) {
                this.$msgbox({
                    title: '错误',
                    message: '请输入加密密码'
                })
                return
            }

            let text = util.Encrypted(this.userPassword, this.content) //加密ID作为检测用户密码输入是否正确的依据

            let id = util.SHA1(text)

            //请求人机验证
            let r = await util.requestAuth(id)

            if (!r) {
                this.$msgbox({
                    title:"提示",
                    message: '请求人机验证失败，请稍后重试'
                })
                return
            }
            this.authCode = r.md5
            this.qrCode = r.qrCode
            this.bShowCaptcha = true
           
            function sleep(ms) {
                return new Promise(resolve => setTimeout(resolve, ms))
            }

            let n = 40

            for (let i = 0; i < n; i++) {
                await sleep(1500)
                r = await util.waitAuth(this.authCode)
                if (r) break;
            }

            if (!r) {
                this.$msgbox({
                    title:"提示",
                    message: '人机验证失败，请稍后重试'
                })
                return
            }

            console.log("授权结果：",r)

            util.InitCos(r.data.credentials)

            console.log("上传")

            r = await util.upload(id, text, () => {
                /* 非必须 */
                console.log("上传taskID:", taskId);
                _taskID = taskId
                this.bUploadDlg = true
            }, () => {
                /* 非必须 */

                this.uploadProgress = parseInt(info.percent * 10000) / 100;
                this.uploadSpeed = parseInt(info.speed / 1024 / 1024 * 100) / 100;
                console.log("progress:", info)
            })

            if (r) {
                //注意，这里必须处理上传成功但提交失败的情况，那么就会出现
                //存储桶保存没有记录的死文件
                r = await util.submit(id)

                if (!r) {
                    this.bUploadDlg = false
                    this.$msgbox({
                        title: '提示',
                        message:'上传失败，请稍后重试'
                    })
                } else {
                    this.$msgbox({
                        title: '提示',
                        message:'上传成功'
                    })
                    console.log("提交结果：", r)
                }
            }else{
                this.$msgbox({
                    title: '提示',
                    message:'上传失败，请稍后重试'
                })
            }



        },
        onDecryp() {
            if (!this.content) {
                this.$msgbox({
                    title: '错误',
                    message: '请先粘贴下载的加密信息'
                })
                return
            }
            if (!this.password) {
                this.$msgbox({
                    title: '错误',
                    message: '请输入解密密码'
                })
                return
            }

            let text = Decrypted(this.password, this.content)
            this.content = text

        }
    }
}
</script>



